.header {
  text-transform: uppercase;
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  align-items: center;
  display: flex;
}

.header__logo-img {
  object-position: center;
  width: 40px;
  height: 40px;
}

.nav__menu {
  flex-direction: row;
  align-items: center;
  margin: 0;
  padding: 0;
  line-height: 60px;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 60px;
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  width: 100%;
  height: 100%;
  display: block;
}

.nav__link:link {
  color: #060b35;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
  cursor: pointer;
  text-decoration: none;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  grid-template-rows: auto;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 487px) {
  .catalog {
    grid-template-columns: 200px;
  }
}

@media (width >= 488px) and (width <= 767px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
    justify-content: center;
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
    justify-content: center;
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
    justify-content: center;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 4px;
  flex-direction: column;
  width: 200px;
  padding: 0 16px 16px;
  transition: transform .3s, background-color .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 32px auto 0;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  align-items: center;
  margin-block: 4px 0;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__ratings {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__review {
  text-align: center;
  color: #060b35;
  align-items: center;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin: 24px 0 16px;
  display: flex;
}

.card__price-text {
  text-align: center;
  color: #616070;
  margin-block: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  margin-block: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__button {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background: url("star-active.edda4c43.svg") no-repeat;
}

body, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}
